.list-box-f{margin-bottom:20px}.list-box-f:last-child{margin-bottom:0}.list-box-f .text{width:144px;float:left;text-align:right;line-height:32px;padding-right:8px}.list-box-f .text>strong{color:red;padding-right:4px}.list-box-f .cont{width:calc(100% - 162px);float:left}.list-box-f .cont .textarea-content .v-input textarea{min-height:400px!important;font-size:12px}.list-box-f .cont>.ans-radio-group,.list-box-f .cont>.v-radio-group{padding-top:7px}.list-box-f .cont>.v-input textarea{height:70px}.list-box-f .cont .v-radio-group-item{font-weight:400;margin-top:1px}