.popup-model{background:#fff;border-radius:3px}.popup-model .top-p{height:70px;line-height:70px;border-radius:3px 3px 0 0;padding:0 20px}.popup-model .top-p>span{font-size:20px}.popup-model .bottom-p{text-align:right;height:72px;line-height:72px;border-radius:0 0 3px 3px;padding:0 20px}.popup-model .content-p{min-width:520px;min-height:100px}