.progress-bar-model{position:relative;margin-right:46px}.progress-bar-model .progress-bar-box{width:100%;height:4px;background:#f6faff;border-radius:4px}.progress-bar-model .progress-bar-box .bar{background:#2d8cf0;border-radius:3px}.progress-bar-model .textp{font-size:12px;color:#fff;display:inline-block}.progress-bar-model .textp span{color:#2d8cf0;font-weight:700}.progress-bar-model .text-1{padding-top:6px}.progress-bar-model .text-2-1{position:absolute;left:-70px;top:-7px}.progress-bar-model .text-2-2{position:absolute;right:-42px;top:-7px}